import React, { Component } from 'react'

export default class Radio extends Component {
    
    render() {

        let options = this.props.question_object.Options.map((current_option) => {
                console.log(current_option.option);
                return (
                    <div className="col-6" key={current_option.option_id}>
                        <input type="radio" name="kbc_option" id={current_option.option_id} key={this.props.question_object.Options.option_id} value={this.props.question_object.Options.option} />
                        <label htmlFor={current_option.option_id}  className="font-weight-bold mx-1">{current_option.option}</label>
                    </div>
                );
            }); 

        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                   {options}
                </div>
            </React.Fragment>
        )
    }
}
