import React, { Component } from 'react'
import "./kbc.css"
import question_bank from "./question_bank"
import Text from "./input_type_text"
import Radio from './input_type_radio'
import Range from "./input_type_range"
import SelectBox from './input_type_select'
import Button from "./button"

export default class Questions extends Component {

    // Answers = [
    //     {no: 1, answer: "not answered"},
    //     {no: 2, answer: "not answered"},
    //     {no: 3, answer: "not answered"},
    //     {no: 4, answer: "not answered"},
    //     {no: 5, answer: "not answered"},
    //     {no: 6, answer: "not answered"},
    //     {no: 7, answer: "not answered"},
    //     {no: 8, answer: "not answered"},
    //     {no: 9, answer: "not answered"},
    //     {no: 10, answer: "not answered"}
    // ];

    state = {
        currentQuestion: 0,
        submited: false,
        rangeValue: 0
    }

    previousQuestion = () => {
        console.log("Previous Question");
        if(this.state.currentQuestion > 0) {
            this.setState({
                currentQuestion: this.state.currentQuestion - 1          
            });
        }
    }

    nextQuestion = () => {
        console.log("next Question");
        if(this.state.currentQuestion < (question_bank.length - 1) ) {
            this.setState({currentQuestion: this.state.currentQuestion + 1});
        }
    }

    render() {

        let Question_Type="";

        switch(question_bank[this.state.currentQuestion].type) {
            case "text":
                // console.log("Options array: ", question_bank[this.state.currentQuestion].Options);
                // console.log("Active Index", this.state.currentQuestion);
                // console.log("Question object", question_bank[this.state.currentQuestion]);

                Question_Type = <Text active_index={this.state.currentQuestion} 
                question_object = {question_bank[this.state.currentQuestion]} />
                break;

            case "radio":
                Question_Type = <Radio active_index={this.state.currentQuestion} 
                question_object = {question_bank[this.state.currentQuestion]} />
                break;

            case "range":
                Question_Type = <Range active_index={this.state.currentQuestion} 
                question_object = {question_bank[this.state.currentQuestion]} rangeValue={this.state.rangeValue} />
                break;

            case "select":
                Question_Type = <SelectBox active_index={this.state.currentQuestion} 
                question_object = {question_bank[this.state.currentQuestion]} rangeValue={this.state.rangeValue} />
                break;
            
            default:
                Question_Type = <Radio active_index={this.state.currentQuestion} 
                question_object = {question_bank[this.state.currentQuestion]} />
        }

            return (
                <React.Fragment>
                    <div className="row justify-content-center my-4">
                        <div className="col-1 py-5">
                            <Button operation={this.previousQuestion} button_usage="Previous" />
                        </div>
                        <div className="col-6 border border-dark my-2">
                            
                            {Question_Type}
                            
                        </div>
                        <div className="col-1 py-5">
                            <Button operation={this.nextQuestion} button_usage="Next" />  
                        </div>
                    </div>
                
                </React.Fragment>
            )
    }
}
