import React, { Component } from 'react'

export default class Range extends Component {
    render() {
        
        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                    <div className="col-6">
                        <input type="range" min="1" max="100" step="1" name="kbc_option" id="kbc_option" className="form-control" />
                        <label htmlFor="kbc_option">{this.props.rangeValue+ "%"}</label>
                    </div>           
                </div>
            </React.Fragment>
        )
    }
}
