import React, { Component } from 'react'

export default class Text extends Component {
    render() {
        let options = this.props.question_object.Options.map((current_option) => {
            console.log(current_option.option_id);
            return(
                <div className="col-6" key={current_option.option_id}>
                    <input type="text" name="kbc_option" className="form-control my-2" />
                </div>
            );
        });

        console.log("text-options_objext", this.props.question_object.Options);
        
        return (
            <React.Fragment>
                <h4 key={this.props.question_object.question_id}>
                    <span className="mx-1">Q.{this.props.active_index + 1}</span>
                    {this.props.question_object.Question}
                </h4>
                <div className="row my-4">
                   {options} 
                </div>
            </React.Fragment>
        )
    }
}
